body{
  background-color: #282c34; /* controls the background colour of app (lower/body section) */
}
form{	
  position: absolute;
  top: 50%;
  left: 50%;
  margin-top: -120px;
  margin-left: -250px;
  width: 500px;
  height: 250px;
  border: 4px dashed #fff;
}
form p{
  width: 100%;
  height: 100%;
  text-align: center;
  line-height: 170px;
  color: #ffffff;
  font-family: Arial;
}
form input{
  position: absolute;
  margin: 0;
  padding: 0;
  width: 100%;
  height: 100%;
  outline: none;
  opacity: 0;
}
form button{
  margin: 20px; /* controls the margin around all buttons */
  color: rgba(0, 0, 0, 0.87);
  background: #e0e0e0 !important;
  border: none;
  width: 200px;
  height: 35px;
  margin-top: -20px;
  margin-left: -4px;
  border-radius: 4px;
  transition: all .2s ease;
  outline: none;
}
form Button:hover{
  background-color: #101820FF  !important;
	color: #ffffff !important;
  font-weight: bolder;
}
form button:active{
  border:0;
}

#runPython{
  margin: 50px;
  color: #fff;
  background: #16a085;
  border: none;
  width: 200px;
  height: 35px;
  margin-top: -20px;
  margin-right: -4px;
  border-radius: 4px;
  border-bottom: 4px solid #117A60;
  transition: all .2s ease;
  outline: none;
}

#uploadedImage{
  position: absolute;
  top: 50%;
  left: 50%;
  margin-top: -100px;
  margin-left: -100px;
  width: 220px;
  height: 200px;
}

/* NB height = 300 px means the upload function will work, but 220 px 
fits the div and will result in button hover feature working better */
#dragDropArea{
  position: absolute;
  top: 10px;
  left: 10px;
  height: 300px;
}

#inferMsg{
  margin-top: 20px;
}

form button:hover{
  background: #101820FF;
	color: #ffffff;
}

.submitBtn{
  height: 35px;
  width: 200px;
}