.App {
  text-align: center;
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 250px;
  pointer-events: none;
}

#tf-logo {
  height: 30vmin;
  align-items: left;
  pointer-events: none;
}

#keras-logo {
  height: 30vmin;
  align-items: right;
  pointer-events: none;
}

.cetech_logo {
  color: white;
  position: absolute;
  width: 200px;
  margin: 10px 0px 0px 10px;
  left: 0;
}

.snowman_icon {
  position: absolute;
  width: 15%;
  right: 0px;
  bottom: 0px;
}

