    animation: App-logo-spin infinite 20s linear;
  }
} */

.App-header {
  background-color: #282c34; /* controls the background colour of app (upper/header section) */
  height: 150px;
  padding: 20px;
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.runPython{
    display: inline-block;
    margin-right: 0.1em;
}

